import React, { useEffect, useState } from "react";
import { tab } from "../../types";
import AccordionItem from "../common/AccordionItem";

const Dekstop = () => {
  const [currentTab, setCurrentTab] = useState(-1);

  const [tabList, setTabList] = useState<tab[]>([]);
  useEffect(() => {
    setTabList([
      {
        title: " ",
        contentList: [
          {
            content:
              "WPF        (100%)",
          },
          {
            content:
              "  API    ,    (100%)",
          },
        ],
      },

      {
        title:
          "BeeCast(  ) Network Monitoring System ",
        contentList: [
          {
            content: "BeeCast   (WPF) (100%)",
          },
        ],
      },
    ]);
  }, []);

  const accodionClickHandler = (e: any) => {
    setCurrentTab(e === currentTab ? -1 : e);
  };

  return (
    <div className="tab-pane p-3 active" role="tabpanel">
      <div className="row">
        <div className="col-12">
          <div className="card">
            <div className="card-header">
              <h6 className="font-15 fw-bold d-block mt-1 mb-1">
                 
              </h6>
            </div>
            <div className="card-body">
              <div className="accordion" id="accordionDesktop">
                {tabList &&
                  tabList.length > 0 &&
                  tabList.map((tab, index) => {
                    return (
                      <AccordionItem
                        title={tab.title}
                        children={tab.contentList}
                        accodionClickHandler={accodionClickHandler}
                        index={index}
                        key={index}
                        currentTab={currentTab}
                      />
                    );
                  })}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dekstop;
